import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatSnackBar} from '@angular/material';

import {Messenger} from '../messenger/messenger.component';
import {ChangepassDialog} from '../changepass-dialog/changepass-dialog.component';
import { NormalService } from 'src/app/normal.service';
import { Router, NavigationEnd } from '@angular/router';
import { OverlayContainer } from '@angular/cdk/overlay';

@Component({
    selector: 'handy-new',
    templateUrl: './handy-new.component.html',
    styleUrls: ['./handy-new.component.scss']
})
export class HandyNewComponent implements OnInit, OnDestroy {
    public qrcodeHide:boolean = true;
    @Input() user;
    isLogin: boolean = false;
    opened: boolean;
    rightOpened: boolean;
    minSequences: any[] = [];
    highSequences: any[] = [];
    public navList: any[] = [
      {
        name: "首页",
        link: "/front",
        icon: "home"
      },
      {
        hideHandy: true,
        name: "真人视讯",
        link: "/真人视讯",
        icon: "camera",
        children: []
      },
      {
        hideHandy: true,
        name: "体育赛事",
        link: "/体育赛事",
        icon: "shutter_speed",
        children: []
      },
      {
        name: "彩票中心",
        icon: "track_changes",
        children: [
          {name: "彩票首页", link: "/lotteryCenter", icon: "local_play"},
          {
            name: "高频彩",
            icon: "more_vert",
          },
          {
            name: "中频彩",
            icon: "more_vert",
          },
          {
            name: "低频彩",
            icon: "vertical_split",
            children: []
          },
        ]
      },
      {
        hideHandy: true,
        name: "棋牌游戏",
        link: "/棋牌游戏",
        icon: "style",
        children: []
      },
      {
        name: "用户中心",
        link: "/user/lobby",
        icon: "account_box",
        // badge: 3
      }
    ];
  
    isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
      .pipe(
        map(result => result.matches)
      );
  
    routerEventsListener: any;
    activeRightTab: number = 0;
    hideRightBar: boolean = false;
  
    chat: any = {
      chanel: "",
      joined: false,
      tempMessage: "",
      messages: [
        {
          m: "尊敬的用户，您好，有什么可以帮助到您的么？",
          senderId: 'admin',
          time: new Date(),
        }
      ]
    };
  
    constructor(
      private router: Router,
      private breakpointObserver: BreakpointObserver,
      public dialog: MatDialog,
      public snackBar: MatSnackBar,
      public normalService: NormalService,
      overlayContainer: OverlayContainer
    ) {
      this.getLotteries();
      overlayContainer.getContainerElement().classList.add('unicorn-dark-theme');
      this.routerEventsListener = this.router.events.subscribe(event=> {
        if (event instanceof NavigationEnd){
          if (this.opened)
           this.opened = !this.opened;
        }
      })
    }
  
    ngOnInit() {
      this.normalService.loginOut$.subscribe(logout => {
        if (logout == true)
          this.router.navigate(["/login"]);
      });
      this.normalService.errorMessage$.subscribe(err=> {
        this.snackBar.open(err.message, "确定", {
          duration: 3000,
        });
      })
      this.normalService.getLottor$.subscribe(lottor=>{
        this.snackBar.open(`您有投注单中奖，中奖金额:${lottor.lottor.Amount.toFixed(2)}, 当前账户余额为:¥${lottor.coin.toFixed(2)}`, '确定', {
          duration: 3000,
        });
      });
      this.normalService.balance$.subscribe(balance => {
        this.snackBar.open(`余额刷新成功，您的当前账户余额为：${balance}元`, "确定", {
          duration: 3000,
        });
      })
      this.opened = false;
      this.normalService.chatMessage$.subscribe(msg=>{
        console.log("msg", msg);
        this.chat.chanel = msg.chanel;
        this.chat.joined = msg.joined;
        this.chat.messages.push({m:msg.msg, senderId: msg.admin?"admin":msg.uid, time: new Date()})
      })
      this.normalService.leaveChat$.subscribe(chanel=>{
        this.chat.chanel = "";
        this.chat.joined = false;
      });
      this.normalService.bonusInfoSubject.subscribe(bonusInfo=>{
        if (bonusInfo.State == 0) {
          this.snackBar.open("上一期红包已抢完，请等待下一期开奖", "确定", {
            duration: 2000
          })
        } else if (bonusInfo.State == 1) {
          this.snackBar.open("红包金额达成，开始抢红包", "确定", {
            duration: 2000
          })
        }
      });
      this.normalService.bonus$.subscribe(bonus=>{
        console.log("getBonus:", bonus);
        if (bonus.code !== 200) {
          this.snackBar.open(bonus.message, "确定",{
            duration: 2000
          })
        } else {
          this.snackBar.open(`${bonus.message},金额为:${bonus.data}元.`,"确定",{
            duration: 3000
          });
          this.normalService.User.Coin += bonus.data;
        }
      });
      this.getBonusState();
    }
  
    ngOnDestroy() {
      this.routerEventsListener.unsubscribe();
    }
  
    getLotteries(): void {
      if (this.normalService.listLotteries.length>0) {
        this.normalService.listLotteries.forEach(element => {
          element.name = element.Name;
          element.link = '/lottery/play/'+element.Id+'/'+element.Name;
          if ([1,4,7,9].indexOf(element.Id)!==-1) {
            this.minSequences.push(element);
          } else {
            this.highSequences.push(element);
          }
        });
      } else {
        this.normalService.getLotteries()
          .subscribe(lotteries => {
              this.normalService.listLotteries = lotteries.data.Records;
              this.normalService.listLotteries.forEach(element => {
                element.name = element.Name;
                element.link = '/lottery/play/'+element.Id+'/'+element.Name;
                if ([1,4,7,9].indexOf(element.Id)!==-1) {
                  this.minSequences.push(element);
                } else {
                  this.highSequences.push(element);
                }
              });
          })
      }
      this.navList[3].children[1].children = this.highSequences;
      this.navList[3].children[2].children = this.minSequences;
    }
  
    showMessenger(title, content) {
      let messenger = this.dialog.open(Messenger, {
        width: '250px',
        data: {title: title, content: content}
      })
    }
  
    async logout() {
      await this.normalService.loginOut();
      // this.router.navigate(['/'])
      window.location.reload();
    }
  
    login() {
      this.router.navigate(["/login"])
    }
  
    logOut() {
      this.normalService.loginOut();
    }
  
    changePassword() {
      if (!this.user.email){
        this.snackBar.open(`您需要先完善您的信息，请确保邮箱地址无误，否则您无法进行修改密码操作`, '确定', {
          duration: 3000,
        })
        return;
      }
      let changePass = this.dialog.open(ChangepassDialog, {
        width: "250px"
      })
  
      changePass.afterClosed().subscribe(async result => {
        console.log('The dialog was closed', result);
        
      });
    }
  
    freshBalance() {
      this.normalService.freshBalance();
    }
  
    hoverNav(nav) {
      nav.childDisplay = !nav.childDisplay;
    }
  
    switchRightTab(index) {
      this.activeRightTab = index;
    }
  
    changeToChat() {
      this.activeRightTab = 1;
    }
  
    changeToBonus() {
      this.activeRightTab = 0;
    }
  
    sendMsg() {
      if (!this.chat.tempMessage) {
        this.snackBar.open("消息不能为空", "确定", {
          duration: 3000,
        });
        return;
      }
      let obj = {msg: this.chat.tempMessage,chanel:this.chat.chanel, uid:`${this.normalService.User.Id}`,joined:this.chat.joined};
      console.log("obj", obj);
      this.normalService.sendMsg(obj);
      this.chat.tempMessage = "";
    }
  
    async getBonusState() {
      let bonus = await this.normalService.getBonusInfo()
    }
  
    geBonus() {
      if (!(this.normalService.BonusObj.State == 1) ) {
        this.snackBar.open("未到开奖时间，请稍等","",{duration:2000})
      }
      this.normalService.getBonus()
    }
  
    hideRight() {
      this.hideRightBar = !this.hideRightBar;
    }
  
    showQrCode() {
      console.log("1111");
      this.qrcodeHide = false;
    }
    hideQrCode() {
      console.log("222");
      this.qrcodeHide = true;
    }
  }
  