import { Component, OnInit } from "@angular/core";
import { NormalService } from '../normal.service';
import { Observable } from 'rxjs';
import { Breakpoints, BreakpointObserver } from '@angular/cdk/layout';
import { map } from 'rxjs/operators';

declare const Swiper;
@Component({
    selector: "lobby",
    templateUrl: "./lobby.template.html",
    styleUrls: ['./header.style.scss','./lobby.style.scss','./content.style.scss']
})
export class LobbyComponent implements OnInit {
    swipers: any[] = [];
    isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches)
    );
    mySwiper: any;
    constructor(
        public normalService: NormalService,
        private breakpointObserver: BreakpointObserver,
    ) {
        this.getNotices();
    }

    ngOnInit() {
        this.normalService.getSwiper(1)
            .subscribe(swiper=>{
                console.log("lobby swiper", swiper);
                if (swiper.code == 200) {
                    this.swipers = swiper.data.Records;
                    setTimeout(()=>{
                        this.mySwiper = new Swiper('.swiper-container1', {
                            autoplay: true,//可选选项，自动滑动
                        })
                    }, 1000)
                }
            })
    }

    getNotices(): void {
        if (this.normalService.notices.length) {
            return;
        } else {
            this.normalService.getNotices()
                .subscribe(notices => {
                })
        }
    }
}