interface option {
    name: "大",
    value: "大",
    bonusIndex: 0,
    type: 1,
    checked: false
};

export class PostBet { // 投注单
    game_id: number;
    game_period: number;
    bet_next: number;
    amount: number;
    bet_more: number;
    bet_higher: number;
    perPeriodNum: number; //隔几期翻bet_higher倍
    bet_win_stop: number;
    bet_list: Bet_list[];
}

export class Bet_list {
    playId: number;
    subId: number;
    subName: string;
    betCode: string;
    betCount: number;
    betMoney: number;
    betEachMoney: number;
    betPrize: string;
    betPrizeShow: string;
    betReward: number;
    betPos: string; //0|1|2|3|4 万、千、百、十、个
    betDbHighPrize: string; // 不需要提交
    betDbLowPrize: string; // 不需要提交
}

