import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { NormalService } from '../normal.service';

@Injectable({
  providedIn: 'root',
})
export class AuthGuard implements CanActivate {
    private user = null;
    private isLogin = false;
    private subscription;

    constructor(private normalService: NormalService, private router: Router) {
    }
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): boolean {
      let key = this.normalService.storage.getItem(this.normalService.storageKey);
      if (key && key!="" && key!="undefined") {
        return true;
      }
      let url: string = state.url;
    // Store the attempted URL for redirecting
    this.normalService.redirectUrl = url;

    // Navigate to the login page with extras
    this.router.navigate(['/login']);
    return false;
  }
}