import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from '@angular/router';
import {FormBuilder, FormGroup, Validators, FormControl, ValidationErrors} from '@angular/forms';
import { NormalService } from '../normal.service';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { MatSnackBar } from '@angular/material';
import {Md5} from "ts-md5";

declare const Swiper;

@Component({
    selector: 'login',
    styleUrls: ['./login.style.scss'],
    templateUrl: './login.template.html',
})
export class LoginComponent implements OnInit {
    register = false;

    isLinear = false;
    firstFormGroup: FormGroup;
    secondFormGroup: FormGroup;

    emailexist: boolean = false;
    usernameexist: boolean = false;
    public registerUser: any = {};
    loginUser: any = {};

    hide = true;
    username = new FormControl('', [Validators.required]);
    password = new FormControl('', [Validators.required]);
    captcha = new FormControl('', [Validators.required]);

    stepperIndex: number = 0;

    captchaId: string;

    isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches)
    );
    constructor (
        private _formBuilder: FormBuilder,
        router: ActivatedRoute,
        private breakpointObserver: BreakpointObserver,
        public snackBar: MatSnackBar,
        private normalService: NormalService,
        private route: Router
    ) {
        router.url.subscribe(url=>{
            if (url.length>0 && url[0].path == "register"){
                this.register = true;
            }
        })
        router.params.subscribe(params=>{
            if (params.referId) {
                this.registerUser.uuid = params.referId;
            }
        })
        this.loginUser.platform = this.normalService.platform;
        this.registerUser.platform = this.normalService.platform;
    }
    ngOnInit() {
        this.firstFormGroup = this._formBuilder.group({
            emailCtrl: ['', Validators.email],
            usernameCtrl: ['', Validators.required]
        });
        this.secondFormGroup = this._formBuilder.group({
            passwordCtrl: ['', Validators.required],
            repasswordCtrl: ['', Validators.required, this.passwordSame],
            coinPasswordCtrl: ['', Validators.required],
            reCoinPasswordCtrl: ['', Validators.required, this.coinPasswordSame],
            captchaCtrl: ['', Validators.required]
        });
        this.getCaptcha();
    }

    getCaptcha() {
        this.normalService.getCaptcha().subscribe(captcha => {
            console.log("captcha",captcha);
            this.captchaId = captcha.captchaId;
            this.loginUser.captcha = '';
            document.getElementById('captcha').setAttribute('src', captcha.data);
        });
    }

    changeCaptcha() {
        this.getCaptcha();
    }

    async passwordSame(control): Promise<any> {
        let passwordCtrl: FormControl = control.parent.controls.passwordCtrl
        if (control.value != passwordCtrl.value) {
            return {"message":"not same"}
        } else {
            return null;
        }
    }

    async coinPasswordSame(control) {
        let coinPasswordCtrl: FormControl = control.parent.controls.coinPasswordCtrl
        if (control.value != coinPasswordCtrl.value) {
            return {"message":"not same"}
        } else {
            return null;
        }
    }

    getErrorMessage() {
        return this.username.hasError('required') ? '该项不能为空' : '';
    }

    login() {
        let LoginUser = JSON.parse(JSON.stringify(this.loginUser));
        LoginUser.captchaId = this.captchaId;
        LoginUser.password = Md5.hashStr(LoginUser.password).toString();
        this.normalService.login(LoginUser).subscribe(user => {
            console.log("loginUser", user);
            if (user.code == 200) {
                this.snackBar.open("登陆成功", "确认", {
                    duration: 3000,
                  });
                this.route.navigate(["/"]);
            } else {
                this.snackBar.open("账号名或密码有误，请重新输入", "确定", {
                    duration: 3000,
                  })
            }
        });
    }

    userVerify(stepper, next = false) {
        // 验证
        if (this.registerUser.username) {
            this.normalService.ifExist(this.normalService.platform, this.registerUser.username).subscribe(res => {
                if (res.data.username) {
                    this.firstFormGroup.controls.usernameCtrl.setErrors({exist: true})
                    return;
                } else if (next == true) {
                    stepper.next()
                }
            })
        } else {
            this.snackBar.open("用户名不能为空","确定", {
                duration: 3000,
              });
        }
        
    }

    signUp(stepper) {
        let RegisterUser = JSON.parse(JSON.stringify(this.registerUser));
        RegisterUser.captchaId = this.captchaId;
        RegisterUser.password = Md5.hashStr(RegisterUser.password).toString();
        this.normalService.signUp(RegisterUser).subscribe(user => {
            console.log("loginUser", user);
            if (user.code == 200) {
                this.snackBar.open(`您的账号已注册成功,并自动登陆，您的用户名为：${user.data.Username}，请保管好您的密码`,"OK", {
                    duration: 3000,
                  });
                stepper.next()
            } else {
                this.snackBar.open(user.message, "确定", {
                    duration: 3000,
                  })
            }
            
        });
    }

    finish() {
        this.route.navigate(['/'])
    }
}