  // 设置选项赔率
  setBonusProp(subs) {
    console.log("setBonusProp", subs, this.temp_bet_list, this.selectedSubId);
    for (let index = 0; index < subs.length; index++) {
      const element = subs[index];
      let bonus = subs[index].BonusProp.split("|");
      let bonusBase = subs[index].BonusPropBase.split("|");
      // console.log("bonus", bonus);
      this.subPlayOptions[subs[index].SubId].optionsArr.forEach( (element, i) => {
        this.subPlayOptions[subs[index].SubId].optionsArr[i].betDbHighPrize = subs[index].BonusProp;
        this.subPlayOptions[subs[index].SubId].optionsArr[i].betDbLowPrize = subs[index].BonusPropBase;

        if (subs[index].SubId == this.selectedSubId) {
          this.temp_bet_list.betDbHighPrize = subs[index].BonusProp;
          this.temp_bet_list.betDbLowPrize = subs[index].BonusPropBase;
        }
        
        element.nums.forEach(el => {
          el.bonusIndex = !el.bonusIndex?0:el.bonusIndex;
          el.bonus = bonus[el.bonusIndex];
          el.bonusBase = bonusBase[el.bonusIndex];
        });
      });
    }
  }

