  // 获取当前选中的投注号码
  getBetList(subId) {
    let betCode: string = "";
    let optionsArr = this.subPlayOptions[subId].optionsArr;
    for (let i = 0; i < optionsArr.length; i++) {
      const element = optionsArr[i];
      for (let j = 0; j < element.nums.length; j++) {
        const el = element.nums[j];
        if (el.checked) {
          if (betCode.length>0 && betCode.charAt(betCode.length - 1)!="|") {
            betCode += "&"
          }
          betCode += el.value;
        }
      }
      if (i<optionsArr.length-1){
        betCode += "|";
      }
    }
    console.log("this.subPlayOptions[this.selectedSubId]", this.subPlayOptions[this.selectedSubId]);
    this.temp_bet_list.betDbHighPrize = this.subPlayOptions[this.selectedSubId].optionsArr[0].betDbHighPrize;
    this.temp_bet_list.betDbLowPrize = this.subPlayOptions[this.selectedSubId].optionsArr[0].betDbLowPrize;
    this.temp_bet_list.playId = this.selectedPlayedId;
    this.temp_bet_list.subId = this.selectedSubId;
    this.temp_bet_list.subName = this.selectedSubName;
    this.temp_bet_list.betCode = betCode;
    
    this.getCount();
    this.temp_bet_list.betCount = this.selectedCountNum;
    return betCode;
  }

  // 获取当前输入的投注号码，计算注数
  getBetListForInput(subId) {
    let str = this.handinput.replace(/^\s+|\s+$/g,"");
    this.temp_bet_list.betCode = str;
    this.temp_bet_list.betDbHighPrize = this.subPlayOptions[this.selectedSubId].optionsArr[0].betDbHighPrize;
    this.temp_bet_list.betDbLowPrize = this.subPlayOptions[this.selectedSubId].optionsArr[0].betDbLowPrize;
    this.temp_bet_list.playId = this.selectedPlayedId;
    this.temp_bet_list.subId = this.selectedSubId;
    this.temp_bet_list.subName = this.selectedSubName;
    console.log("this.temp_bet_list", this.temp_bet_list);
    this.getCountForInput(str);
    this.temp_bet_list.betCount = this.selectedCountNum;
    return this.handinput;
  }

  // 为单式输入计算注数
  getCountForInput(str) {
    let inputArr = str.split(/,| |\n|\t/);
    let currentCount = inputArr.length
    inputArr.forEach(code=>{
      let elArr = [];
      let step = 1;
      for (let index = 0; index < code.length; index+=step) {
          elArr.push( code.substring(index,index+step) )
      }
      let rightLenth: number;
      if ([108].indexOf(this.selectedSubId) != -1) {
        rightLenth = 5;
      }
      if ([106,140].indexOf(this.selectedSubId) != -1) {
          rightLenth = 4;
      }
      if ([89,95,96,55,61,62,129,132,134].indexOf(this.selectedSubId) != -1) {
          rightLenth = 3;
      }
      if ([39,47,123,126].indexOf(this.selectedSubId) != -1) {
          rightLenth = 2;
      }
      if (elArr.length!==rightLenth) {
        currentCount--
        if (currentCount == 0) {
          this.selectedCountNum = 0;
        }
        return
      }
      let hasDouble: boolean;
      for (let ii = 0; ii < elArr.length-1; ii++) {
        for (let iii = ii+1; iii < elArr.length; iii++) {
          if (elArr[ii] == elArr[iii]) {
            hasDouble = true;
          }
        }
      }
      if ([95,61,132].indexOf(this.selectedSubId) != -1) { // 组三 /必须有对
        if (!hasDouble) {
          currentCount--
          if (currentCount == 0) {
            this.selectedCountNum = 0;
          }
          return
        }
        if (elArr[0] == elArr[1] && elArr[1] == elArr[2]) { //豹子号
          currentCount--
          if (currentCount == 0) {
            this.selectedCountNum = 0;
          }
          return
        }
      }
      if ([96,62,47,126,134].indexOf(this.selectedSubId) != -1) { // 任二组选或组六 /不能有对
        if (hasDouble) {
          currentCount--
          if (currentCount == 0) {
            this.selectedCountNum = 0;
          }
          return
        }
      }
    })
    if (this.fangAnNum>1) {
      currentCount*=this.fangAnNum;
    }
    this.selectedCountNum = currentCount;
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
   
      // TODO: send the error to remote logging infrastructure
      if (error.statusText == "Unauthorized"){
        console.error(error.statusText); // log to console instead
        // this.loginOut();
      }
   
      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);
   
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

