  getCount223() {
    let count = 0;
    let minusNum = 0;
    let optionsArr = this.subPlayOptions[this.selectedSubId].optionsArr;
    for (let i = 0; i < optionsArr.length; i++) {
      const element = optionsArr[i];
      for (let ii = 0; ii < element.nums.length; ii++) {
        const el = element.nums[ii];
        if (!el.checked) {
          continue;
        }
        for (let j = i+1; j < optionsArr.length; j++) {
          const elem = optionsArr[j];
          for (let jj = 0; jj < elem.nums.length; jj++) {
            if (!elem.nums[jj].checked) {
              continue;
            }
            count++;
            if (elem.nums[jj].value == el.value) {
              minusNum++;
            }
          }
        }
      }
    }
    count-=minusNum;
    return count;
  }

