  // 交集
  arrIntersect(a: string[], b: string[]): number { //传字符串
    let count = 0;
    for (let i = 0; i < b.length; i++) {
      let temp = b[i]
      for (let j = 0; j < a.length; j++) {
        if (temp == a[j]) {
          count += 1
          break
        }
      }
    }
    return count;
  }

  Combination(c:number, b:number) {
    if (c == 0) {
      let optionsArr = this.subPlayOptions[this.selectedSubId].optionsArr;
      let countArr = Array.from({length: optionsArr.length}, item => 0);
      for (let i = 0; i < optionsArr.length; i++) {
        const element = optionsArr[i];
        for (let j = 0; j < element.nums.length; j++) {
          const el = element.nums[j];
          if (el.checked) {
            c++;
          }
        }
      }
    }
    if (b == 0 || c == 0) {
      return 0
    }
    if (b > c) {
      return 0
    }
    if (b > c/2) {
      b = c - b
    }
    let a = 0;
    for (let i = c; i >= (c - b + 1); i--) {
      a += Math.log(i)
    }
    for (let i = b; i >= 1; i--) {
      a -= Math.log(i)
    }
    a = Math.exp(a)
    let ii = Math.floor(a)
    return ii;
  }

  CombinationInstrument(c:number, b:number) {
    if (b == 0 || c == 0) {
      return 0
    }
    if (b > c) {
      return 0
    }
    if (b > c/2) {
      b = c - b
    }
    let a = 0
    for (let i = c; i >= (c - b + 1); i--) {
      a += Math.log(i)
    }
    for (let i = b; i >= 1; i--) {
      a -= Math.log(i)
    }
    a = Math.exp(a)

    let ii = Math.round(a)
    return ii;
  }

