  getCount142_144() {
    let tempCount = 0;
    let optionsArr = this.subPlayOptions[this.selectedSubId].optionsArr;
    let countArr = [];
    for (let i = 0; i < optionsArr.length; i++) {
      const element = optionsArr[i];
      for (let j = 0; j < element.nums.length; j++) {
        const el = element.nums[j];
        if (el.checked) {
          if (!countArr[i])
            countArr[i] = [];
          countArr[i].push(el.name);
        }
      }
    }
    let need = 2
    if (this.selectedSubId == 144) {
      need = 1
    }
    if (!countArr[0] || !countArr[1]) {
      return 0;
    }
    let h = this.arrIntersect(countArr[0], countArr[1])
    let tmpNums = this.CombinationInstrument(countArr[0].length, 1) * this.CombinationInstrument(countArr[1].length, need);
    console.log("tmpNums", tmpNums);
    if (h > 0) { //交集个数
      if (this.selectedSubId == 142) {
        tmpNums -= this.CombinationInstrument(h, 1) * this.CombinationInstrument(countArr[1].length-1, 1)
      } else if (this.selectedSubId == 144) { //C(m,1)*C(n,1)-C(h,1)
        tmpNums -= this.CombinationInstrument(h, 1)
      }
    }

    tempCount += tmpNums
    //fmt.Println(tempCount, "	", tempStrCountLeft, "	", tempStrCountRight)
    return tempCount
  }

