import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HandyComponent } from './handy/handy.component';
import { AppComponent } from './app.component';
import { HammerCardComponent } from './hammer-card/hammer-card.component';
import { LoginComponent } from './login/login.component';
import { PageNotFoundComponent } from './pageNotFound/pageNotFound.component';
import { LobbyComponent } from './lobby/lobby.component';
import { AuthGuard } from './permission/auth.guard';
import { HandyNewComponent } from './handy-new/handy-new.component';

const routes: Routes = [
  { path: 'center', component: HandyComponent, canActivate: [AuthGuard], children: [
    {path: '', component: HammerCardComponent},
    {path: 'game', loadChildren: './+gamefront/gamefront.module#GameFrontModule'},
    {path: 'game/play', loadChildren: './+gamePlay/gamePlay.module#GamePlayModule'},
  ]},
  { path: '', component: HandyNewComponent, canActivate: [AuthGuard], children: [
    { path: '', redirectTo: 'lotteryCenter', pathMatch: 'full' },
    { path: 'lotteryCenter', loadChildren: './+caipiaohandyfront/caipiaohandyfront.module#CaipiaoHandyFrontModule' },
    { path: 'lottery/play', loadChildren: './+caipiaobusiness/caipiaobusiness.module#CaipiaoBusinessModule' },
    {path: 'user', loadChildren: './+user/user.module#UserModule'}
  ]},
  { path: 'login', children: [
    { path: '', component: LoginComponent, pathMatch: 'full'},
    { path: 'register', component: LoginComponent, pathMatch: 'full'},
    { path: 'register/:referId', component: LoginComponent, pathMatch: 'full'},
  ]},
  { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
